<template>
  <div class="zd-common-component">
    <Form class="zd-cm-applyForm" ref="provideForm" :model="provideForm" :rules="ruleValidate" :label-width="85" inline>
      <!--<FormItem label="入库单号：" prop="outOrder">
        <Input v-model.trim="provideForm.outOrder" placeholder="入库单号" class="zd-common-w150 zd-common-mr10" :maxlength="30"></Input>
      </FormItem>-->
      <FormItem label="入库日期：" prop="date">
        <DatePicker v-model="provideForm.date" :editable="false" type="date" placement="bottom-end" format="yyyy-MM-dd"
                    placeholder="入库日期" class="zd-common-input-h38 zd-common-w150 zd-common-mr10"
                    ></DatePicker>
      </FormItem>
      <FormItem label="入库仓库：" prop="warehouse">
        <Select v-model="provideForm.warehouse" placeholder="选择入库仓库" class="zd-common-w150 zd-common-mr10">
          <Option v-for="item in warehouseArr" :value="item.id" :key="item.id">{{ item.name }}</Option>
        </Select>
      </FormItem>
      <FormItem label="入库类型：" prop="type">
        <Select v-model="provideForm.type" placeholder="选择入库类型" class="zd-common-w150 zd-common-mr10">
          <Option v-for="item in typeArr" :value="item.code" :key="item.code">{{ item.name }}</Option>
        </Select>
      </FormItem>
      <FormItem label="供应商：" :label-width="60">
        <Select v-model="provideForm.supplier" placeholder="全部供应商" class="zd-common-w150 zd-common-mr10">
          <Option v-for="item in supplierArr" :value="item.id" :key="item.id">{{ item.name }}</Option>
        </Select>
      </FormItem>
      <FormItem label="购买人：" :label-width="60">
        <Select
          ref="purchaser"
          class="zd-common-input-h38 zd-common-w150 zd-common-fl zd-common-mr10"
          placeholder="购买人"
          v-model="provideForm.purchaser"
          filterable
          remote
          :remote-method="remoteMethod"
          :loading="loading">
          <Option v-for="option in leaderArr" :value="option.id" :key="option.id">{{option.name}}</Option>
        </Select>

      </FormItem>
    </Form>
    <div class="zd-common-black-b zd-common-pt5 zd-common-pb5 zd-common-pl20">
      <Button type="default" class="zd-common-mr10 zd-common-h32 zd-common-lh8" @click="addGoodsHandle">
        添加
      </Button>
      <Button type="default" class="zd-common-mr10 zd-common-h32 zd-common-lh8" @click="delRowHandle">
        删除
      </Button>
      <Button type="default" class="zd-common-mr10 zd-common-h32 zd-common-lh8" @click="importHandle">
        导入
      </Button>
      <Input v-model="code" placeholder="请输入物品编码，按Enter添加" :maxlength="50" class="zd-cm-w220 zd-common-input-h32"
             @keyup.enter.native="getGoodsByCodeHandle"/>
    </div>
    <Form ref="formGoodsData" :model="formGoodsData" class="zd-common-mt10">
      <Table class="zd-common-overflow-v" border :columns="goodsCols" :data="formGoodsData.attrData" show-summary
             :summary-method="handleSummary" :row-class-name="rowClassName" @on-row-click="clickRowHandle">
        <template slot-scope="{ row,index }" slot="number">
          <Form-item class="zd-common-mt24" :prop="'attrData.' + index + '.number'"
                     :rules="{required: true, message:'不能为空', trigger:'change',type:'number'}">
            <InputNumber :max="9999" :min="1" :precision='0' placeholder="数量"
                         v-model="formGoodsData.attrData[index].number" @on-change="getCurrIndex(index)"/>
          </Form-item>
        </template>
        <template slot-scope="{ row,index }" slot="price">
          <Dropdown placement="bottom-start" trigger="click" @on-click="setHistoryPrice">
            <Form-item class="zd-common-mt24" :prop="'attrData.' + index + '.price'"
                       :rules="{required: true, message:'不能为空', trigger:'change',type:'number'}">
              <InputNumber :min="0.01" :precision='2' placeholder="价格" v-model="formGoodsData.attrData[index].price"
                           @on-change="getCurrIndex(index)"/>
            </Form-item>
            <DropdownMenu slot="list">
              <DropdownItem v-for="item in formGoodsData.attrData[index].priceList" :key="item" :name="item">{{ item
                }}</DropdownItem>
            </DropdownMenu>
          </Dropdown>
        </template>
        <!--<template slot-scope="{ row,index }" slot="purchaser">
          <Form-item class="zd-common-mt24" :prop="'attrData.' + index + '.purchaser'"
                     :rules="{required: true, message:'不能为空', trigger:'blur'}">
            <Input :precision='0' placeholder="购买人" v-model="formGoodsData.attrData[index].purchaser"/>
          </Form-item>
        </template>-->
        <template slot-scope="{ row,index }" slot="amount">
          <Form-item class="zd-common-mt24">
            <InputNumber :min="1" :precision='2' placeholder="金额" v-model="formGoodsData.attrData[index].amount"/>
          </Form-item>
        </template>
      </Table>
    </Form>
    <div class="zd-common-mt10 zd-common-clearFix">
      <span class="zd-common-fl">制单人：{{ userName }}</span>
      <span class="zd-common-fl zd-common-ml20">制单日期：{{ date }}</span>
    </div>
    <Form ref="provideRemarkForm" :model="provideRemarkForm" class="zd-common-mt20">
      <FormItem label="备注：" class="zd-common-mb0">
        <Input style="width: 826px;" class="zd-common-fl" v-model.trim="provideRemarkForm.remark" placeholder="备注" :maxlength="100" type="textarea" :autosize="{minRows: 5,maxRows: 5}"></Input>
      </FormItem>
    </Form>
    <div slot="footer" class="zd-common-modal-footer zd-common-pt30">
      <Button type="primary" class="zd-common-mr35 zd-common-w100 zd-common-h32 zd-common-lh8" @click="handleSubmit"
              :loading="saveLoading">
        <span v-if="!saveLoading">确定</span>
        <span v-else>正在保存...</span>
      </Button>
      <Button @click="toPutIn" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
    </div>
    <!--添加物品-->
    <Modal v-model="addGoodsModal" :mask-closable="false" :width="1200" >
      <h3 slot="header" class="zd-common-modal-header">
        <span>物品档案</span>
      </h3>
      <add-goods ref="child"></add-goods>
      <div slot="footer" class="zd-common-modal-footer zd-common-pt10">
        <div slot="footer" class="zd-common-modal-footer zd-common-pt10">
          <Button type="primary" class="zd-common-mr35 zd-common-w100 zd-common-h32 zd-common-lh8" @click="saveGoodsHandle">确定</Button>
          <Button @click="addGoodsModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
        </div>
      </div>
    </Modal>
    <!--导入-->
    <Modal v-model="importModal" :mask-closable="false">
      <h3 slot="header" class="zd-common-modal-header">
        <span>导入物品档案</span>
      </h3>
      <Form ref="importForm" :model="importForm" :rules="ruleValidate" :label-width="100" inline>
        <FormItem label="上传文件："  prop="fileName">
          <Upload
            ref="upload"
            action="//"
            accept="application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
            :before-upload="handleUpload"
          >
            <Button type="primary" icon="ios-cloud-upload-outline">选择文件</Button>
          </Upload>
          <div v-if="importForm.file !== null"> {{ importForm.file.name }} </div>
          <a :href="serverPath+'/sr/storeroomData!downloadIntoTemplate.action'">下载模板</a>
          <input type="text" v-model="importForm.fileName" hidden/>
        </FormItem>
      </Form>
      <import-msg :importErrorMsg="importErrorMsg"></import-msg>
      <div slot="footer" class="zd-common-modal-footer zd-common-pt10">
        <Button type="primary" class="zd-common-mr35 zd-common-w100 zd-common-h32 zd-common-lh8"
                @click="importSubmit('importForm')" :loading="saveLoading">
          <span v-if="!saveLoading">确定</span>
          <span v-else>正在保存...</span>
        </Button>
        <Button @click="importModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import AddGoods from "../../components/addGoods";
  import ImportMsg from '../../components/importMsg';
  import {utils} from "../../../comm/script/utils";

  export default {
    components:{
      AddGoods,
      ImportMsg
    },
    data(){
      return {
        //添加入库单
        warehouseArr:[],
        typeArr:[],
        supplierArr:[],
        provideModal:false,
        provideForm:{
          date:new Date().toLocaleDateString(),
          warehouse:'',
          type:'',
          supplier:'',
          purchaser:''
        },
        applyDepartArr:[],
        goodsCols:[
          {
            type: 'index',
            title:'序号',
            align: 'center',
            width:65,
            key:'sortNum'
          },
          {
            title: '物品名称',
            align: 'center',
            key: 'name',
          },
          {
            title: '物品编码',
            align: 'center',
            key: 'code',
          },
          {
            title: '物品分类',
            align: 'center',
            key: 'classify',
          },
          {
            title: '品牌',
            align: 'center',
            key: 'brand',
          },
          {
            title: '规格/型号',
            align: 'center',
            key: 'model',
          },
          {
            title: '单位',
            align: 'center',
            key: 'unit',
          },
          {
            title: '当前库存',
            align: 'center',
            key: 'currentInventory',
          },
          {
            title: '入库数量',
            align: 'center',
            slot: 'number',
            key: 'number',
            renderHeader:(h,params)=>{
              return h('div',[
                h('span', {
                  class:'zd-cm-required'
                },'入库数量'),
              ])
            }
          },
          {
            title: '价格',
            align: 'center',
            slot: 'price',
            key: 'price',
            renderHeader:(h,params)=>{
              return h('div',[
                h('span', {
                  class:'zd-cm-required'
                },'价格'),
              ])
            }
          },
          /*{
            title: '购买人',
            align: 'center',
            slot: 'purchaser',
          },*/
          {
            title: '金额',
            align: 'center',
            slot: 'amount',
            key: 'amount',
          }
        ],
        formGoodsData:{
          attrData:[]
        },
        provideRemarkForm:{
          remark:''
        },
        ruleValidate: {
          date: [
            { required: true, type: 'date', message:'不能为空', trigger: 'change' }
          ],
          warehouse: [
            { required: true, message:'不能为空', trigger: 'change' }
          ],
          type: [
            { required: true, message:'不能为空', trigger: 'change' }
          ],
        },
        saveLoading:false,
        leaderArr:[],
        loading:false,
        date:'',
        userName:'',
        //添加物品
        addGoodsModal:false,
        delId:'',
        delIndex:'',
        //设置历史价格
        curIndex:null,
        //通过code添加
        code:'',
        //修改
        editId:this.$route.params.id,
        editOrderNO:'',
        //导入
        importModal:false,
        importForm:{
          file:null,
          fileName:''
        },
        importErrorMsg:[],
      }
    },
    created(){
      this.getIntoKindList().then((response)=>{
        this.typeArr = response.data;
      });
      this.getGoodsSupplierAllEnable().then((response)=>{
        this.supplierArr = response.data;
      });
      this.getChargedStoreRoom().then((response)=>{
        this.warehouseArr = response.data;
      });
      const e = new Date();
      this.date = e.getFullYear() +"-"+ this.fillZero(e.getMonth()+1) +"-"+ this.fillZero(e.getDate());
      this.$store.dispatch('getBaseInfo').then((res) => {
        this.remoteMethod(res.userName);
      });
      this.getIntoWarehouseRecordInfo(this.editId);
    },
    computed:{
      /*userName(){
        return this.$store.state.userName;
      },*/
    },
    methods:{
      //修改获取详情
      getIntoWarehouseRecordInfo(id) {
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getIntoWarehouseRecordInfo.action',
          data:{
            id:id
          },
        })
          .then((response) => {
              let aData = response.data;
              this.provideForm.date = new Date(aData.date);
              this.provideForm.warehouse = aData.warehouseId;
              this.provideForm.type = aData.kindCode;
              this.provideForm.supplier = aData.goodsSupplierId;
              this.provideForm.purchaser = aData.buyserId;
              this.provideRemarkForm.remark = aData.note;
              aData.goodsList.map((item)=>{
                this.formGoodsData.attrData.push(
                  {
                    id:item.id,
                    name:item.name,
                    code:item.code,
                    classify:item.goodsKind,
                    brand:item.brand,
                    model:item.size,
                    unit:item.unit,
                    number:item.count,
                    currentInventory:item.inventory,
                    price:parseInt(item.unitPrice),
                    amount:item.sum,
                    priceList:[]
                  }
                )
              })
              this.editOrderNO = aData.num;
              this.editId = aData.id;
              this.userName = aData.userName;
              this.date = aData.productDate;
          })
          .catch((error) => {
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },

      //日期
      changeDate(val){
        this.provideForm.date = val;
      },

      //回到入库单列表
      toPutIn(){
        this.$router.push({path:'/putIn'});
      },

      //合计
      handleSummary ({ columns, data }) {
        const sums = {};
        columns.forEach((column, index) => {
          const key = column.key;
          if (index === 0) {
            sums[key] = {
              key,
              value: '合计'
            };
            return;
          }
          const values = data.map(item => Number(item[key]));
          if (!values.every(value => isNaN(value)) && (index===8 || index===10)) {
            const v = values.reduce((prev, curr) => {
              const value = Number(curr);
              if (!isNaN(value)) {
                return prev + curr;
              } else {
                return prev;
              }
            }, 0);
            sums[key] = {
              key,
              value: v
            };
          } else {
            sums[key] = {
              key,
              value: ''
            };
          }
        });
        return sums;
      },

      //选择购买人
      remoteMethod(name){
        this.selectLeader(name).then((res)=>{
          this.leaderArr = res.data;
        })
      },

      //添加物品
      addGoodsHandle(){
        if(this.provideForm.warehouse === ""){
          this.postWarningTip('请先选择入库仓库');
          return
        }
        this.addGoodsModal=true;
        let addGood = this.formGoodsData.attrData;
        let addGoodId = [];
        addGood.map((item)=>{
          addGoodId.push(item.id);
        });
        this.$refs.child.getKind('0');
        this.$refs.child.selectedArr = addGood.slice();
        this.$refs.child.selectedArrId = addGoodId;
      },
      saveGoodsHandle(){
        this.addGoodsModal = false;
        this.formGoodsData.attrData = this.$refs.child.selectedArr.slice();
        this.getInventoryByParam(this.$refs.child.selectedArrId.join(','),this.provideForm.warehouse);
      },

      //根据物品 仓库获取库存
      getInventoryByParam(goodsInfoIds,warehouseId) {
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getInventoryByParam.action',
          data:{
            goodsInfoIds:goodsInfoIds,
            warehouseId:warehouseId,
          },
        })
          .then((response) => {
            let aData = response.data;
            for (let key in aData){
              this.formGoodsData.attrData.map((item)=>{
                if(item.id === key){
                    this.$set(item,'currentInventory',aData[key].inventory);
                    this.$set(item,'priceList',aData[key].priceList);
                }
              })
            }
          })
          .catch((error) => {
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },

      //通过code添加物品
      getGoodsByCodeHandle(){
        if(this.code === ""){
          this.postWarningTip('请输入物品编码进行添加');
          return
        }
        this.getGoodsByCode(this.code);
      },
      getGoodsByCode(code){
        const ids = this.formGoodsData.attrData.map((item)=>{
          return item.id;
        })
        this.getGoodsInfoByUseType('0','','',code).then((res)=>{
          let aData = res.data;
          aData.map((item)=>{
            if(ids.indexOf(item.id)<0){
              this.formGoodsData.attrData.push(
                {
                  id:item.id,
                  name:item.name,
                  code:item.code,
                  classify:item.goodsKind,
                  brand:item.brand,
                  model:item.size,
                  unit:item.unit,
                  number:null,
                  currentInventory:null,
                  price:null,
                  amount:null,
                  priceList:[]
                }
              )
              this.getInventoryByParam(item.id,this.provideForm.warehouse);
            }
          })
          this.code = "";

        });
      },

      //显示历史价格
      setHistoryPrice(name){
        this.formGoodsData.attrData[this.curIndex].price = parseInt(name);
        this.formGoodsData.attrData[this.curIndex].amount = this.formGoodsData.attrData[this.curIndex].price*this.formGoodsData.attrData[this.curIndex].number;
      },
      getCurrIndex(index){
        this.curIndex = index;
        this.formGoodsData.attrData[this.curIndex].amount = this.formGoodsData.attrData[this.curIndex].price*this.formGoodsData.attrData[this.curIndex].number;
      },

      //保存入库单
      saveOrUpdateIntoWarehouseRecord(date,warehouse,type,supplier,buyer,note,goodsArr,code,id) {
        let params = {};
        params['warehouseRecord.date'] = date;
        params['warehouseRecord.warehouse.id'] = warehouse;
        params['warehouseRecord.intoKind'] = type;
        if(supplier!==""){
          params['warehouseRecord.goodsSupplier.id'] = supplier;
        }
        if(buyer!==""){
          params['warehouseRecord.buyer.id'] = buyer;
        }
        params['warehouseRecord.note'] = note;
        params['warehouseRecord.code'] = code;
        params['warehouseRecord.id'] = id;
        goodsArr.map((item,i)=>{
          params['intoWarehouseGoodsVOs['+i+'].goodsInfoId'] = item.id;
          params['intoWarehouseGoodsVOs['+i+'].count'] = item.number;
          params['intoWarehouseGoodsVOs['+i+'].unitPrice'] = item.price;
          params['intoWarehouseGoodsVOs['+i+'].sum'] = item.amount;
        });
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!saveOrUpdateIntoWarehouseRecord.action',
          data:params,
        })
          .then((response) => {
              this.saveLoading = false;
              this.toPutIn();
          })
          .catch((error) => {
            this.saveLoading = false;
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },

      //保存验证
      handleSubmit(){
        let flag = false,flag2 = false;
        this.$refs['provideForm'].validate((valid) => {
          if (valid) {
            flag = true;
          }
        });
        if(this.formGoodsData.attrData.length<=0){
          this.postWarningTip('请添加物品');
        }else{
          this.$refs['formGoodsData'].validate((valid) => {
            if(valid){
              flag2 = true;
            }
          });
          if(flag && flag2){
            this.saveLoading = true;
            const e = new Date(this.provideForm.date);
            const date = e.getFullYear() +"-"+ this.fillZero((e.getMonth()+1)) +"-"+ this.fillZero(e.getDate());
            this.saveOrUpdateIntoWarehouseRecord(date,this.provideForm.warehouse,this.provideForm.type,this.provideForm.supplier,this.provideForm.purchaser,this.provideRemarkForm.remark,this.formGoodsData.attrData,this.editOrderNO,this.editId);
          }
        }
      },

      //删除添加的物品
      clickRowHandle(row,index){
        if(this.delId === row.id){
          this.delId = "";
          this.delIndex = "";
        }else{
          this.delId = row.id;
          this.delIndex = index;
        }
      },
      rowClassName(row, index) {
        if (this.delId === row.id) {
          return 'demo-table-info-row';
        }
        return '';
      },
      delRowHandle(){
        if(this.delIndex === ""){
          return this.postWarningTip('请选择删除的物品');
        }else{
          this.formGoodsData.attrData.splice(this.delIndex,1);
          if(this.$refs.child.selectedArrId.indexOf(this.delId)>-1){
            let index = this.$refs.child.selectedArrId.indexOf(this.delId);
            this.$refs.child.selectedArr.splice(index,1);
            this.$refs.child.selectedArrId.splice(index,1);
          }
        }
      },

      //导入
      importHandle(){
        if(this.provideForm.warehouse === ""){
          this.postWarningTip('请先选择入库仓库');
          return
        }
        this.importErrorMsg = [];
        this.importModal = true;
        this.$refs['importForm'].resetFields();
        this.importForm.file = null;
      },
      handleUpload(file) {
        if(!utils.regImportType.test(file.name)){
          this.postWarningTip(utils.regImportTypeMsg);
        }else{
          this.importForm.file = file;
          this.importForm.fileName = file.name;
          this.$refs['importForm'].validateField('fileName');
        }
        return false;
      },
      importSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.saveLoading=true;
            this.saveImport();
          }
        })
      },
      saveImport(){
        let formData = new FormData();
        formData.append('id',this.provideForm.warehouse);
        formData.append('upload',this.importForm.file);
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!importIntoGoodsInfo.action',
          data:formData,
          processData: false,
          contentType: false,
          transformRequest: [function (data) {
            return data;
          }],
        })
          .then((response) => {
            let aData = response.data;
            if(aData.errorCode === '1'){
              this.importErrorMsg = aData.msg;
            }else{
              this.importModal=false;
              this.importForm.file = null;
              aData.listData.map((item)=>{
                this.formGoodsData.attrData.push(
                  {
                    id:item.id,
                    name:item.name,
                    code:item.code,
                    classify:item.goodsKind,
                    brand:item.brand,
                    model:item.size,
                    unit:item.unit,
                    number:item.count,
                    currentInventory:item.inventory,
                    price:item.unitPrice,
                    amount:item.sum,
                    priceList:[]
                  }
                )
              })
            }
            this.saveLoading=false;
          })
          .catch((error) => {
            this.saveLoading=false;
            this.importModal=false;
            this.postErrorTip('请求失败');
            this.$refs.upload.value = '';
            console.log(error);
          });
      }
    },
    mounted(){}
  }
</script>
<style lang="less" scoped>

</style>



